import React from "react";
import { useNavigate } from "react-router-dom"; // React Router 사용

interface TableRowProps {
  title: string;
  part: string;
  career: string;
  onClick?: () => void; // 클릭 핸들러 추가
}

const Table: React.FC = () => {
  const navigate = useNavigate();

  const rows: TableRowProps[] = [
    {
      title: "현재 진행 중인 채용 공고가 없습니다.",
      part: "",
      career: "",
      // onClick: () => navigate("/company/job-detail/1"), // 클릭 시 이동할 경로 설정
    },
  ];

  return (
    <div>
      <div className="overflow-x-auto">
        <table className="min-w-full border-y border-neutral-300">
          <tbody className="">
            {rows.map((row, index) => (
              <tr
                key={index}
                className=" hover:bg-orange-50"
                onClick={row.onClick}
              >
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.title}
                </td>
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.part}
                </td>
                <td className="px-2 py-3 text-base md:text-lg md:py-4 text-neutral-800">
                  {row.career}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Table;
