import React from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정

const LanguageSwitcher: React.FC = () => {
  const { setLanguage, language } = useLanguage(); // setLanguage와 language 사용

  const handleLanguageChange = (newLanguage: "ko" | "en") => {
    setLanguage(newLanguage); // 언어 변경
  };

  return (
    <div className="text-sm font-normal text-neutral-600">
      <button
        className="px-2 py-0.5 mr-2 text-orange-500 transition border border-orange-500 rounded-full hover:font-bold"
        onClick={() => handleLanguageChange("ko")}
        disabled={language === "ko"}
      >
        한국어
      </button>
      <button
        className="px-2 py-0.5 text-orange-500 transition border border-orange-500 rounded-full hover:font-bold"
        onClick={() => handleLanguageChange("en")}
        disabled={language === "en"}
      >
        English
      </button>
    </div>
  );
};

export default LanguageSwitcher;
