// src/components/Header.tsx
import React, { useState } from "react";
import { Link } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import logo from "../assets/images/logo.png";
import LanguageSwitcher from "./LanguageSwitcher";

const Header: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen((prev) => !prev);
  };

  return (
    <header>
      <div className="top-0 z-50 flex justify-center w-full py-4 mx-auto text-black bg-white border border-neutral-300 md:fixed">
        <nav className="max-w-[1200px] w-full flex flex-col px-2 md:px-0">
          {/* Top-right: Language Switcher */}
          <div className="flex justify-end w-full">
            <LanguageSwitcher />
          </div>
          <div className="flex justify-between my-2">
            {/* Middle-left Logo */}
            <div className="flex items-center w-full">
              <img
                src={logo}
                alt="ROKIT Genomics Logo"
                className="w-8 h-auto mr-2"
              />
              <Link
                to="/"
                className="text-2xl font-extrabold transition duration-200 hover:text-orange-600 whitespace-nowrap"
              >
                ROKIT Genomics
              </Link>
            </div>
            {/* Middle-right Navigation Links (desktop) */}
            <div className="flex justify-end w-full">
              <div className="hidden space-x-8 text-lg font-medium md:flex">
                {/* Company dropdown */}
                <div className="relative group">
                  <Link to="/company/aboutus" className="hover-orange-text">
                    Company
                  </Link>
                  <div className="box-dropdown">
                    <Link to="/company/aboutus" className="hover-orange-text">
                      About Us
                    </Link>
                    <Link to="/company/careers" className="hover-orange-text">
                      Careers
                    </Link>
                    <Link to="/company/notice" className="hover-orange-text">
                      Notice
                    </Link>
                  </div>
                </div>

                {/* Services dropdown */}
                <div className="relative group">
                  <Link
                    to="/services/scrna"
                    className="hover:text-orange-600 whitespace-nowrap"
                  >
                    Services
                  </Link>
                  <div className="box-dropdown">
                    <Link
                      to="/services/scrna"
                      className="hover:text-orange-600 whitespace-nowrap"
                    >
                      Single Cell RNA Sequencing
                    </Link>
                    <Link
                      to="/services/ngs"
                      className="hover:text-orange-600 whitespace-nowrap"
                    >
                      NGS
                    </Link>
                    <Link
                      to="/bioinformatics"
                      className="hover:text-orange-600 whitespace-nowrap"
                    >
                      Bioinformatic Analysis
                    </Link>
                  </div>
                </div>
                <Link
                  to="/prcenter"
                  className="hover:text-orange-600 whitespace-nowrap"
                >
                  PR Center
                </Link>
                <Link
                  to="/blog"
                  className="hover:text-orange-600 whitespace-nowrap"
                >
                  Blog
                </Link>
                <Link
                  to="/contact"
                  className="hover:text-orange-600 whitespace-nowrap"
                >
                  Contact Us
                </Link>
              </div>

              {/* Hamburger button (for mobile view) */}
              <div className="block md:hidden">
                <IconButton edge="end" aria-label="menu" onClick={toggleMenu}>
                  <MenuIcon />
                </IconButton>
              </div>
            </div>
          </div>
        </nav>
      </div>
      {/* Mobile Menu (header 아래에 위치) */}
      {isOpen && (
        <div className="z-0 flex flex-col py-8 space-y-4 text-lg font-medium bg-neutral-100 md:hidden">
          <Link
            to="/company/aboutus"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            About Us
          </Link>
          <Link
            to="/company/careers"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            Careers
          </Link>
          <Link
            to="/company/notice"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            Notice
          </Link>

          <Link
            to="/services/scrna"
            onClick={toggleMenu}
            className="px-8 hover:text-orange-600"
          >
            Single Cell RNA Sequencing Service
          </Link>
          <Link
            to="/services/ngs"
            onClick={toggleMenu}
            className="px-8 hover:text-orange-600"
          >
            NGS Service
          </Link>
          <Link
            to="/bioinformatics"
            onClick={toggleMenu}
            className="px-8 hover:text-orange-600"
          >
            Bioinformatic Analysis
          </Link>
          <Link
            to="/prcenter"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            PR Center
          </Link>
          <Link
            to="/blog"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            Blog
          </Link>
          <Link
            to="/contact"
            onClick={toggleMenu}
            className="px-8 hover:text-gray-500"
          >
            Contact Us
          </Link>
        </div>
      )}
    </header>
  );
};

export default Header;
