import React, { ReactNode } from "react";

interface HoverCardProps {
  title: string;
  description: string;
}

export const HoverCard: React.FC<HoverCardProps> = ({ title, description }) => {
  return (
    <div className="max-w-sm p-6 transition transform bg-white border border-gray-200 rounded-lg shadow-md hover:shadow-lg">
      <h3 className="text-lg font-bold text-gray-800">{title}</h3>
      <p className="mt-2 text-sm text-gray-600">{description}</p>
    </div>
  );
};

interface ImageCardProps {
  title: string;
  children: ReactNode;
  image: string;
  onClick?: () => void; // 클릭 핸들러 추가
}

export const ImageCard: React.FC<ImageCardProps> = ({
  title,
  children,
  image,
  onClick,
}) => {
  return (
    <div
      className={`max-w-sm p-4 transition transform bg-white border shadow-md border-neutral-200 rounded-xl hover:shadow-xl ${
        onClick ? "cursor-pointer active:scale-95" : "cursor-default"
      }`}
      onClick={onClick}
    >
      <div className="relative w-full rounded-lg aspect-w-4 aspect-h-3">
        <img
          src={image}
          alt={title}
          className="absolute inset-0 object-contain object-center w-full h-full rounded-lg"
        />
      </div>
      <p className="p-bold">{title}</p>
      {children}
    </div>
  );
};
