import React, { useState, ReactNode } from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정

interface BIProps {
  title: string;
  text: string;
  option: string;
  image: string;
}

const Basic: React.FC<BIProps> = ({ title, text, option, image }) => {
  return (
    <div className="flex-container">
      <div className="flex-grow space-y-4">
        <h2>{title}</h2>
        <div className="flex-shrink-0 max-w-[700px]">
          <img src={image} alt={title} />
        </div>
        <p>{text}</p>
        <p className="p-light">{option}</p>
      </div>
    </div>
  );
};

const Second: React.FC<BIProps> = ({ title, text, option, image }) => {
  return (
    <div className="flex-container">
      <div className="flex-grow space-y-4">
        <h2>{title}</h2>
        <p>{text}</p>
        <p className="p-light">{option}</p>
      </div>
      <div className="flex-shrink-0 max-w-[600px]">
        <img src={image} alt={title} />
      </div>
    </div>
  );
};

const Bioinformatics = () => {
  const { t } = useLanguage();

  // 원하는 인덱스 지정
  const basicindexes = [1, 2, 3, 4, 5];
  const secondindexes = [1, 2, 3, 4, 5];

  const getFilteredData = (indexes: number[]) => {
    return indexes.map((index) => ({
      id: index, // 고유 ID 추가
      title: t("BI", `title-${index}`),
      text: t("BI", `text-${index}`),
      option: t("BI", `option-${index}`),
      image: t("BI", `image-${index}`),
    }));
  };

  const basicpackage = getFilteredData(basicindexes);
  const secondpackage = getFilteredData(secondindexes);

  // 하위 탭 상태
  const [activeBasicTab, setActiveBasicTab] = useState(basicpackage[0].id); // 기본 Basic 탭
  const [activeSecondTab, setActiveSecondTab] = useState(secondpackage[0].id); // 기본 Second 탭

  return (
    <div>
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h1>{t("BI", "main-title")}</h1>
            <h3>{t("BI", "main-text")}</h3>
          </div>
        </div>
      </div>
      {/* 상위 섹션 1 */}
      <div className="bg-white section-main">
        <div className="section-main-container">
          <div className="flex-container min-h-[400px]">
            <div className="flex-grow-0 basis-1/4">
              <h1>{t("BI", "package-1")}</h1>
              <p>첫 번째 패키지 텍스트</p>
            </div>
            <div className="flex flex-col flex-grow-0 basis-3/4">
              {/* 하위 탭 콘텐츠 */}
              <div className="order-1 md:order-2">
                {basicpackage
                  .filter((item) => item.id === activeBasicTab)
                  .map((item) => (
                    <Basic
                      key={item.id}
                      title={item.title}
                      text={item.text}
                      option={item.option}
                      image={item.image}
                    />
                  ))}
              </div>

              {/* 하위 탭 버튼 */}
              <div className="flex justify-center order-2 my-4 space-x-2 md:justify-start md:border-b md:order-1">
                {basicpackage.map((item) => (
                  <button
                    key={item.id}
                    onClick={() => setActiveBasicTab(item.id)}
                    className={`flex space-x-1 text-lg items-center transition-colors ${
                      activeBasicTab === item.id
                        ? "text-orange-500 md:border-orange-500 md:border-b-2"
                        : "text-neutral-900 hover:text-orange-500"
                    } ${
                      // 모바일: dot만 표시, 데스크탑: 텍스트와 border 표시
                      "w-3 h-3 md:w-auto md:h-auto md:px-2 md:py-2"
                    }`}
                  >
                    {/* dot (모바일에서만 표시) */}
                    <div
                      className={`rounded-full ${
                        activeBasicTab === item.id
                          ? "bg-orange-500"
                          : "bg-neutral-400"
                      } w-3 h-3 md:hidden`}
                    />
                    {/* 텍스트 (데스크탑에서만 표시) */}
                    <span className="hidden md:inline">{item.title}</span>
                  </button>
                ))}
              </div>
            </div>
          </div>
          {/* 상위 섹션 2 */}
          <div className="bg-white section-main">
            <div className="section-main-container">
              <div className="min-h-[400px]">
                <div className="title-container">
                  <h1>{t("BI", "package-2")}</h1>
                  <p>두 번째 패키지 텍스트</p>
                </div>
                <div className="flex flex-col">
                  {/* 하위 탭 콘텐츠 */}
                  <div className="order-1 md:order-2">
                    {secondpackage
                      .filter((item) => item.id === activeSecondTab)
                      .map((item) => (
                        <Second
                          key={item.id}
                          title={item.title}
                          text={item.text}
                          option={item.option}
                          image={item.image}
                        />
                      ))}
                  </div>
                  {/* 하위 탭 버튼 */}
                  <div className="flex justify-center order-2 my-4 space-x-2 md:order-1 md:justify-start md:border-b">
                    {secondpackage.map((item) => (
                      <button
                        key={item.id}
                        onClick={() => setActiveSecondTab(item.id)}
                        className={`flex space-x-1 text-lg font-medium transition-colors ${
                          activeSecondTab === item.id
                            ? "md:border-b-2 border-orange-500 text-orange-500"
                            : "text-neutral-600 hover:text-orange-500"
                        } ${
                          // 모바일: dot만 표시, 데스크탑: 텍스트와 border 표시
                          "w-3 h-3 md:w-auto md:h-auto md:px-4 md:py-2"
                        }`}
                      >
                        {/* dot (모바일에서만 표시) */}
                        <div
                          className={`rounded-full ${
                            activeSecondTab === item.id
                              ? "bg-orange-500"
                              : "bg-neutral-400"
                          } w-3 h-3 md:hidden`}
                        />
                        <span className="hidden md:inline">{item.title}</span>
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Bioinformatics;
