import React, { useState } from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import Genome from "./ServicesGenome";
import Transcriptome from "./ServicesTranscriptome";
import Epigenome from "./ServicesEpigenome";
import Metagenome from "./ServicesMetagenome";
import { useNavigate } from "react-router-dom";

const NGS = () => {
  const { t } = useLanguage();
  const [isOpen, setIsOpen] = useState(false);
  const toggleMenu = () => setIsOpen(!isOpen);
  const navigate = useNavigate();

  return (
    <div>
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <h1>{t("Service-NGS", "main-title")}</h1>
          <h3>{t("Service-NGS", "main-text")}</h3>
          <button className="button-bold" onClick={() => navigate("/contact")}>
            {t("Button", "contact")}
          </button>
        </div>
      </div>
      <Genome />
      <Transcriptome />
      <Epigenome />
      <Metagenome />
      <div className="bg-gradient-to-tr from-orange-200 to-orange-50 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <div className="items-center justify-between flex-container">
              <div className="flex-row flex-auto">
                <h1>{t("Service-NGS", "contact-main-title")}</h1>
                <h3>{t("Service-NGS", "contact-main-text")}</h3>
              </div>
              <div className="flex justify-center flex-auto">
                <button className="button-bold">
                  {t("Button", "contact")}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NGS;
