import React from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import { useNavigate } from "react-router-dom"; // React Router 사용

interface BlogProps {
  title: string;
  text: string;
  date: string;
  image: string;
  onClick: () => void;
}

const Blog = () => {
  const { t } = useLanguage();
  const navigate = useNavigate();

  const rows: BlogProps[] = [
    {
      title: "2024년 가을 뉴스레터",
      text: "2024년 3분기를 맞이하여 로킷제노믹스에서 준비한 새로운 소식을 전해드립니다.",
      date: "2025.01.01.",
      image: "/genomics2024/images/[뉴스레터]24년3분기_그림.png",
      onClick: () => navigate("/blog/newsletter"), // 클릭 시 이동할 경로 설정
    },
  ];

  return (
    <div>
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h1>{t("Blog", "main-title")}</h1>
            <h3>{t("Blog", "sub-title")}</h3>
          </div>
        </div>
      </div>
      <div className="bg-white section-main">
        <div className="section-main-container">
          <div className="flex flex-col">
            {rows.map((row, index) => (
              <div
                key={index}
                className="cursor-pointer flex-container group"
                onClick={row.onClick}
              >
                {/* 이미지 */}
                <div className="flex-shrink-0 w-[300px] h-32 transition-transform duration-300 group-hover:scale-105">
                  <img
                    src={row.image}
                    alt={row.title}
                    className="object-cover w-full h-full rounded-md"
                  />
                </div>
                {/* 텍스트 콘텐츠 */}
                <div className="flex flex-col flex-grow">
                  <h2 className="text-2xl transition-colors duration-300 group-hover:text-orange-600">
                    {row.title}
                  </h2>
                  <p className="text-lg">{row.text}</p>
                  <span className=" text-neutral-500">{row.date}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Blog;
