import React from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import MarkdownRenderer from "../components/MarkdownRenderer";
import { Carousel } from "../components/Carousel";
import { ImageCard } from "../components/Card"; // ImageCard 컴포넌트 임포트

const BlogMarkdown: React.FC = () => {
  const { id } = useParams<{ id: string }>(); // URL의 :id 값 가져오기

  const filePath = `${process.env.PUBLIC_URL}/content/${id}.md`; // Markdown 파일 경로

  const navigate = useNavigate();

  const handleNavigate = () => {
    navigate("/services/scrna");
  };

  const blogItems = [
    {
      title: "2024년 가을 뉴스레터",
      image: "/genomics2024/images/[뉴스레터]24년3분기_그림.png",
      date: "2025.01.01.",
      onClick: () => navigate("/blog/newsletter"),
    },
  ];

  return (
    <div className="bg-white section-main">
      <div className="section-main-container">
        <div className="markdown-container min-h-[900px] flex justify-center">
          <div className="w-[900px]">
            <MarkdownRenderer filePath={filePath} />
            <div className="flex flex-col items-center justify-center my-24">
              <h2>로킷제노믹스만의 scRNA-seq 서비스에 대해 더 알아보세요.</h2>
              <div className="button-bold" onClick={handleNavigate}>
                서비스 알아보기
              </div>
            </div>
            {/* <div className="w-full my-24">
              <h2>다른 포스트 둘러보기</h2>
              <Carousel
                items={blogItems}
                visibleItemsCount={3}
                renderItem={(item) => (
                  <ImageCard
                    title={item.title}
                    image={item.image}
                    onClick={item.onClick}
                  >
                    <div className="text-base text-neutral-500">
                      {item.date}
                    </div>
                  </ImageCard>
                )}
              />
            </div> */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogMarkdown;
