import React from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import GridLayout from "../components/GridLayout";

const PrCenter = () => {
  const { t } = useLanguage();

  return (
    <div>
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h1>{t("PrCenter", "main-title")}</h1>
            <h3>{t("PrCenter", "main-text")}</h3>
          </div>
        </div>
      </div>
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <GridLayout />
        </div>
      </div>
    </div>
  );
};

export default PrCenter;
