// src/components/Footer.tsx
import React, { useState } from "react";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import { Link } from "react-router-dom";

const Footer: React.FC = () => {
  const { t } = useLanguage();

  return (
    <footer className="flex flex-col items-center w-full py-4 mx-auto bg-neutral-900">
      <div className="flex flex-col max-w-[1200px] w-full">
        <div className="flex flex-col ml-4 md:flex-row md:ml-0">
          <div className="w-full py-4 text-lg md:text-xl md:w-1/4 text-neutral-100">
            {t("Footer", "company-name")}
          </div>
          <div className="w-full text-sm break-keep md:text-lg font-extralight text-neutral-300">
            <div className="py-2 md:py-4">{t("Footer", "introduce")}</div>
            <div className="flex flex-col justify-between md:flex-row">
              <div className="w-full mb-4 md:w-1/2 md:mb-0">
                {t("Footer", "company-registration-number-title")}
                <br />
                {t("Footer", "company-registration-number")}
              </div>
              <div className="w-full mb-4 md:w-1/2 md:mb-0">
                {t("Footer", "tel-title")} {t("Footer", "tel")}
                <br /> {t("Footer", "email-title")} {t("Footer", "email")}
              </div>
            </div>
          </div>
        </div>
        <div className="py-8 text-sm font-normal text-center">
          <div className="text-neutral-400">
            &copy; ROKIT Genomics. All rights reserved.
          </div>
          <Link to="/privacy" className="text-neutral-500 hover:underline">
            {t("Footer", "privacy")}
          </Link>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
