import React from "react";
import { useParams } from "react-router-dom";

const JobDetails: React.FC = () => {
  const { id } = useParams<{ id: string }>(); // URL에서 jobId 가져오기

  // 채용 공고 데이터
  const jobDetails = [
    {
      id: "1",
      title: "싱글셀 BI 분석가 채용",
      part: "분석팀",
      career: "경력 무관",
      description: "싱글셀 데이터 분석 및 연구에 참여할 인재를 찾습니다.",
    },
    {
      id: "2",
      title: "데이터 엔지니어 채용",
      part: "데이터팀",
      career: "신입/경력",
      description: "데이터 파이프라인 구축 및 관리에 관심 있는 분을 찾습니다.",
    },
  ];

  // id에 해당하는 공고 데이터 찾기
  const job = jobDetails.find((job) => job.id === id);

  // id에 해당하는 공고가 없을 때 처리
  if (!job) {
    return (
      <div className="p-8">
        <h1 className="text-2xl font-bold">해당 공고를 찾을 수 없습니다.</h1>
      </div>
    );
  }

  // id에 해당하는 공고가 있을 때 렌더링
  return (
    <div className="p-8">
      <h1 className="text-2xl font-bold">{job.title}</h1>
      <p className="text-lg text-gray-600">{job.part}</p>
      <p className="text-lg text-gray-600">{job.career}</p>
      <p className="mt-4">{job.description}</p>
    </div>
  );
};

export default JobDetails;
