import React from "react";
import { useParams } from "react-router-dom";
import MarkdownRenderer from "../components/MarkdownRenderer";

const NoticeMarkdown: React.FC = () => {
  const { id } = useParams<{ id: string }>(); // URL의 :id 값 가져오기

  const filePath = `${process.env.PUBLIC_URL}/content/${id}.md`; // Markdown 파일 경로

  return (
    <div className="bg-white section-main">
      <div className="section-main-container">
        <div className="markdown-container flex justify-center min-h-[900px]">
          <div className="w-[900px]">
            <MarkdownRenderer filePath={filePath} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default NoticeMarkdown;
