import React, { createContext, useContext, useState, ReactNode } from "react";
import en from "../locales/en.json";
import ko from "../locales/ko.json";

// 다국어 데이터를 관리하는 객체의 타입 정의
// type Translations = {
//   en: Record<string, string>;
//   ko: Record<string, string>;
// };

type Translations = {
  en: Record<string, Record<string, string>>; // 각 언어별로 페이지별 텍스트 객체
  ko: Record<string, Record<string, string>>; // 각 언어별로 페이지별 텍스트 객체
};

// `translations` 객체의 타입을 명시적으로 정의
const translations: Translations = { en, ko };

// 컨텍스트 타입 정의
interface LanguageContextProps {
  language: "en" | "ko"; // 언어 타입을 'en'과 'ko'로 제한
  setLanguage: (lang: "en" | "ko") => void;
  t: (page: string, key: string) => string; // `t` 함수는 페이지 이름과 키를 받아서 번역을 반환
}

// 컨텍스트 생성
const LanguageContext = createContext<LanguageContextProps | null>(null);

// 컨텍스트 프로바이더 컴포넌트
interface LanguageProviderProps {
  children: ReactNode; // children의 타입 지정
}

export const LanguageProvider: React.FC<LanguageProviderProps> = ({
  children,
}) => {
  const [language, setLanguage] = useState<"en" | "ko">("ko"); // 기본 언어를 한국어로 설정

  // 번역 함수
  const t = (page: string, key: string): string => {
    return translations[language][page]?.[key] || key; // 페이지와 키에 맞는 번역을 반환
  };

  return (
    <LanguageContext.Provider value={{ language, setLanguage, t }}>
      {children}
    </LanguageContext.Provider>
  );
};

// 컨텍스트 소비를 위한 커스텀 훅
export const useLanguage = (): LanguageContextProps => {
  const context = useContext(LanguageContext);
  if (!context) {
    throw new Error("useLanguage must be used within a LanguageProvider");
  }
  return context;
};
